body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.box {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: linear-gradient(to right, rgba(1, 27, 114, 0.6) 0%, rgba(1, 27, 114, 0.6) 100%), url(../img/hero.jpg);
  height: auto;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  padding: 4rem 0;
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header {
    max-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header {
    height: 70rem;
  }
}
@media (min-width: 1024px) {
  .header {
    height: 90rem;
  }
}
.header__link {
  transition-property: transform;
  transition-duration: 1s;
}
.header__link:hover {
  transform: rotate(180deg);
}
.header__log {
  width: 12rem;
}
@media (min-width: 768px) {
  .header__log {
    width: 6rem;
  }
}
.header__bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .header__bar {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__box {
  position: relative;
}
.header__trophy {
  display: none;
  width: 36rem;
  animation-name: animateTrophy;
  animation-duration: 4s;
  animation-iteration-count: 1;
}
@media (min-width: 768px) {
  .header__trophy {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
  }
}

@keyframes animateTrophy {
  0% {
    opacity: 0;
    transform: translateX(30rem);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
.nav {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  position: relative;
  z-index: 1;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    gap: 3rem;
  }
}
.nav__link {
  color: #FFFFFF;
  font-weight: 700;
}
.nav__link:hover {
  color: #01C5FF;
}

@media (min-width: 768px) {
  .content {
    width: 50%;
  }
}
.content__image {
  width: 30rem;
  margin: 0 auto;
  margin-top: 2rem;
}
.content__description {
  margin: 3rem 0;
  padding: 0 3rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .content__description {
    text-align: left;
  }
}
.content__link {
  background-color: #011B72;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  margin-top: 1rem;
  border-radius: 0.4rem;
}
@media (min-width: 768px) {
  .content__link {
    width: auto;
  }
}
.content__link:hover {
  background-color: #01C5FF;
}

.about {
  background-image: linear-gradient(to right, rgba(1, 27, 114, 0.97) 0%, rgba(1, 27, 114, 0.97) 100%), url(../img/about.jpg);
  background-repeat: no-repeat;
