.memory {
  background-color: #011B72;
  text-align: center;
  color: #FFFFFF;
  border-radius: 0.5rem;
  width: auto;
  height: 30rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.memory__title {
  text-transform: uppercase;
  font-weight: 700;
  margin: 0;
  padding-top: 1.5rem;
}
.memory__content {
  opacity: 0;
  padding: 5.5rem 0;
}
.memory__content:hover {
  opacity: 1;
}
.memory__description {
  margin: 0;
  padding: 1rem 3rem;
}
.memory:nth-child(1) {
  background-image: linear-gradient(to right, rgba(1, 27, 114, 0) 0%, rgba(1, 27, 114, 0) 100%), url("../img/memory1.jpg");
  background-repeat: no-repeat;
  background-position: center, center center;
  background-size: cover;
}
.memory:nth-child(1):hover {
  background-image: linear-gradient(to right, rgba(1, 27, 114, 0.8) 0%, rgba(1, 27, 114, 0.8) 100%), url("../img/memory1.jpg");
}
.memory:nth-child(2) {
  background-image: linear-gradient(to right, rgba(1, 27, 114, 0) 0%, rgba(1, 27, 114, 0) 100%), url("../img/memory2.jpg");
  background-repeat: no-repeat;
  background-position: center, center center;
  background-size: cover;
