.rankings {
  background-image: linear-gradient(to right, rgba(1, 27, 114, 0.97) 0%, rgba(1, 27, 114, 0.97) 100%), url(../img/about.jpg);
  padding-bottom: 5rem;
}
.rankings__heading {
  text-transform: uppercase;
  font-family: "Abyssinica SIL", serif;
  text-align: center;
  margin: 0;
  color: #FFFFFF;
  padding: 2rem 0;
}
.rankings__title {
  font-family: "Abyssinica SIL", serif;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 2.5rem;
  margin: 0;
  padding: 1rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .rankings__title {
    text-align: left;
  }
}

.rank {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  padding: 3rem 4rem;
}
@media (min-width: 1024px) {
  .rank {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.team {
  margin: 0 auto;
}
.team__content {
  text-align: center;
}
.team__image {
  height: 10rem;
  margin: 0 auto;
  transition-property: transform;
  transition-duration: 0.5s;
}
@media (min-width: 768px) {
  .team__image {
    height: 15rem;
  }
}
.team__image:hover {
  transform: scale(1.5) translateY(2.2rem);
  z-index: 2;
}
.team__rank {
  margin: 0;
  padding-top: 1.5rem;
  font-weight: 700;
  font-size: 2.2rem;
  line-height: 1;
  color: #011B72;
}
.team__name {
  margin: 0;
  font-weight: 700;
  color: #01C5FF;
}

.footer {
  background-color: #011B72;
  text-align: center;
  padding: 4rem 0;
}

.myFooter {
  background-color: #011B72;
  margin-top: 3rem;
}
.myFooter__terms {
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.3rem;
}

.firma {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.firma__year {
  color: #FFFFFF;
  margin: 0 0.5rem;
}
.firma__name {
  font-weight: 900;
  font-size: 2rem;
  color: #FFFFFF;
  margin: 0;
}

.logs {
  border-top: solid #FFFFFF;
  padding: 3rem 0 0 0;
  margin-bottom: 3rem;
  display: flex;
  justify-content: center;
}
.logs__image {
  width: 6rem;
  margin: 0 3rem;
  transition-property: transform background-size;
  transition-duration: 1s;
}
.logs__image:hover {
  transform: scale(1.4);
}